@import "../../variabiles/colors/colors";

.footer {
  background-color: $primaryColor;
}

.footer__container {
  padding: 90px 0;
  display: flex;
  justify-content: space-between;
}

.footer__left {
  max-width: 340px;
  display: flex;
  flex-direction: column;
  row-gap: 95px;

  p {
    margin-top: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $whiteColor;
  }
}

.footer__left-logo{
  color: $whiteColor;
  font-weight: 800;
  font-size: 18px;
  line-height: 18px;
}

.footer__left-bot {
  display: flex;
  flex-direction: column;
  row-gap: 7px;

  span {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: #D6D5DF;
  }
}

.footer__right {
  display: flex;
  flex-direction: column;
  max-width: 340px;
  width: 100%;
}

.footer__right-title {
  color: $whiteColor;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}

.footer__right-form {
  padding: 8px;
  background-color: white;
  border-radius: 4px;
  margin-top: 12px;
  display: flex;
  gap: 5px;
  width: 100%;

  & input::placeholder {
    color: $secondaryColor;
  }

  & input {
    padding-left: 10px;
    border: none;
    width: 100%;
    outline: none;
  }
}

.form__icon {
  padding: 13px 12px;
  background-color: $toggleColor;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}

.footer__right-description {
  color: $whiteColor;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  margin-top: 10px;
}

.footer__right-container {
  display: flex;
  column-gap: 30px;
  margin-top: 30px;
}

.container__item {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: $thirdColor;
  display: flex;
  align-items: center;
  justify-content: center;

  & svg path:first-child{
    fill: white;
  }
}

@media screen and (max-width: 790px) {
  .footer__container {
    flex-direction: column;
    gap: 60px;
    align-items: center;
  }
}

@media screen and (max-width: 600px) {
  .footer__left {
    gap: 50px;
  }

  .footer__left-logo {
    font-size: 16px;
  }

  .footer__left-top  p{
    font-size: 12px;
  }

  .footer__left-bot span {
    font-size: 12px;
  }

  .footer__right-title {
    font-size: 12px;
  }

  .footer__right-form input {
    padding: 5px;
  }

  .form__icon {
    height: 35px;
    width: 35px;
  }

  .footer__right-description {
    font-size: 12px;
  }
}

